.popup {
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-right: 17px;
  transition: opacity 0.4s ease;
  opacity: 0;
  visibility: hidden;
}

@media screen and (max-width: 768px) {
  .popup {
    padding: 0;
  }
}
